import React from "react";
import SearchBar from "./SearchBar";
import youtube from "../apis/youtube";
import VideoList from "./VideoList";
import VideoDetails from "./VideoDetails";

class App extends React.Component{
    state={videos:[] , selectedVideo:null}
    
    // will provide default values to the page rather than showing loading blank page
    componentDidMount(){
        this.onTermSubmit("cats");
    }

    onTermSubmit=async (term)=>{
        const response=await youtube.get("/search",{
            params:{
                q:term
            }
        });
        console.log(response.data.items);
        this.setState({
            videos:response.data.items,
            selectedVideo:response.data.items[0]
        });
    }

    onSelectVideo=(video)=>{
        console.log("from app.js ", video);
        this.setState({selectedVideo:video});
    }

    render(){
        return (
            <div className="ui container">
                <SearchBar  onFormSubmit={this.onTermSubmit}/>
                <p>I have {this.state.videos.length} videos</p>

                {/* using semantic-ui grid system 
                    each row of grid is divided into 16 columns*/}
                <div className="ui grid">
                    <div className="ui row">
                        <div className="eleven wide column">
                            <VideoDetails video={this.state.selectedVideo}/>
                        </div>
                        <div className="five wide column">
                            <VideoList videos={this.state.videos} onSelectVideo={this.onSelectVideo}/>
                        </div>
                    </div>

                </div>
                
                
            </div>
        );

    }
}
export default App;