import React from "react";

const VideoDetails=(props)=>{
    if(!props.video){
        return(
            <div>Loading...</div>
        );
    }
    const videoUrl=`https://www.youtube.com/embed/${props.video.id.videoId}`
    return(
        <div className="ui segment">
            <div className="ui header">{props.video.snippet.title}</div>
            
            {/* Will tell the semantic ui that we are using <iframe> so sematic ui will provide 
                styling to that tag */}
            <div className="ui embed">
                <iframe title={props.video.snippet.title} src={videoUrl}></iframe>
            </div>
            <div className="ui segment">
                <p>
                    {props.video.snippet.description}
                </p>
            </div>
                        
        </div>
        
    );
}

export default VideoDetails;