import React from "react";

class SearchBar extends React.Component{
    state={term:""}
    
    onInputChange=(event)=>{
        this.setState({term:event.target.value});
    }
    
    onFormSubmit=(event)=>{
        //the main prupose of preventDefault is to stop browser from submitting the form 
        //so by writing we prevent browser from submitting and refreshing the page .
        event.preventDefault();
        
        //onFormSubmit is not the one in this class its the one we have passed from the App class
        //as prop
        this.props.onFormSubmit(this.state.term);
    }

    render(){
        return(
            <div className="search-bar ui segment">
                <form onSubmit={this.onFormSubmit} className="ui form">
                    
                    <div className="field">
                        <label>Video Search</label>
                        <input 
                            type="text"
                            value={this.state.term}
                            onChange={this.onInputChange}
                        />
                    </div>
                
                </form>
            </div>

        );

    }
}

export default SearchBar;